@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:200,300,400,700);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.left-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 2px;
}

.heading {
  color: white;
  font-size: 5em;
  letter-spacing: 5px;
  margin-bottom: 0;
  margin-top: 0;
  padding-top: 30px;
}

.subheading {
  color: white;
  font-size: 1em;
}

.subheading a {
  color: #61dafb;
}

.about {
    background-color: #28334A;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Helvetica;
    font-weight: 300;
    padding-bottom: 50px;
}

#aboutMeImg {
    border-radius: 50%;
    width: 215px;
    margin: 30px;
    border: 1px #ccc solid;
}

#about-me-text {
    width: 40%;
    margin: 0 auto;
    text-align: center;
    line-height: 150%;
}
body {
    margin: 0;
}

main {
    margin: 16px;
}

.navbar {
    display: flex;
    align-items: center;
    background: #212a3b;
    color: white;
    font-family: Helvetica;
    font-weight: 300;
    width: 100%;
    position: fixed;
    transition: 0.5s;
    z-index: 1;
}

.fixed {
    background: black;
}

.navbar__title {
    margin-right: auto;
    font-size: 150%;
    vertical-align: middle;
    padding: 16px 16px;

}

.navbar__item {
    padding: 16px 16px;
    cursor: pointer;
    vertical-align: middle;
    transition: 0.5s;
}

.navbar__item:hover {
    color: #61dafb;
}
.landing {
    background-color: #212a3b;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    min-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.landing img {
    border-radius: 50%;
    border: 2px #ccc solid;
    width: 50vh;
    height: 50vh;
    margin-top: 70px;
}

.mainheading {
    color: whitesmoke;
    font-size: 4em;
    margin: 0 auto;
    margin-top: 3vh;
    /* word-spacing: 5px; */
    font-family: Arial, sans-serif;
}

.secondheading {
    color: whitesmoke;
    font-size: 2.4em;
    margin: 0 auto;
    margin-top: 3vh;
    /* word-spacing: 5px; */
    font-family: Arial, sans-serif;
}

.links-holder {
    display: flex;
    flex-direction: row;
    height: 100px;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

.links-holder a {
    font-size: 3em;
    color: #61dafb;
    text-decoration: none;
    margin: 0 25px;
}

.links-holder a:hover {
    -webkit-filter: opacity(70%);
            filter: opacity(70%)
}

.scrollDown {
    color: whitesmoke;
    font-size: 4em;
    background: none;
    border: none;
    outline: none;
    margin-top: auto;
    line-height: 75%;
}

.scrollDown:hover {
    color: #61dafb;
    cursor: pointer;
}
.experience-view {
    display: flex;
    flex-direction: row;
    border-radius: 7%;
    padding: 20px;
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
}

#imgCol {
    width: 35%;
    /* justify-content: center; */
}

#imgCol img {
    width: 60%;
    border-radius: 5%;
    margin-bottom: 50px;
}

#textCol {
    width: 60%;
    margin-left: 5%;
    text-align: left;
}

#jobTitle {
    color: #61dafb;
    font-weight: 300;
    font-size: 1.8em;
    letter-spacing: 0;
    margin: 0;
}

#companyName {
    color: gold;
    font-size: 1.5em;
    font-weight: 300;
    margin-top: 6px;
    margin-bottom: 0;
    padding: 0;
}

#dateRange {
    color: white;
    font-size: 1em;
    font-weight: 300;
    padding: 0;
    margin-top: 6px;
}

#description {
    line-height: 150%;
}



.experience {
    background-color: #282c34;
    font-family: Helvetica;
    font-weight: 300;
    position: relative;
}

#left-experience-bar
{
    background-color: gold;
}

.experience h2 {
    color: white;
    font-size: 4em;
    font-weight: 300;
    letter-spacing: 5px;
    margin-bottom: 0;
    margin-top: 0;
    padding-top: 30px;
}

#experience-subheading {
    margin-bottom: 40px;
}

.experience a{
    color: #61dafb;
    text-decoration: underline;
}
.project-thumb-view {
    display: flex;
    flex-direction: row;
    border-radius: 7%;
    padding: 20px;
    max-width: 1000px;
    margin-bottom: 20px;
}

.imgCol {
    width: 35%;
    /* display: flex;
    align-items: center; */
}

.imgCol img {
    width: 80%;
    border-radius: 5%;
    margin-top: 10px;
}

.textCol {
    width: 60%;
    margin-left: 5%;
}

.title-holder{
    display: flex;
    flex-direction: row;
}

.title {
    color: #61dafb;
    font-size: 2em;
    text-align: left;
    margin-top: 0;
    margin-bottom: 0;
}

#projectGithub {
    color: #61dafb;
    font-size: 2em;
    width: 40px;
    display: flex;
    justify-content: left;
    align-items: center;
    margin: 0;
    text-align: left;
    text-decoration: none;
}

.project-date {
    color: whitesmoke;
    font-size: 1em;
    margin: 0;
    margin-top: 10px;
    text-align: left;
}

.description {
    color: whitesmoke;
    font-size: 1.3em;
    text-align: left;
    line-height: 130%;
}

#demoLink a {
    color: #61dafb;
    font-size: 1em;
    text-align: left;
    text-decoration: underline;
}


.projects {
    background-color: #28334A;
    font-family: Helvetica;
    font-weight: 300;
    position: relative;
}

#left-projects-bar
{
    background-color: #61dafb;
}

.projects-collection {
    width: 100%;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}

.footer {
    background-color: black;
    position: relative;
    font-weight: 300;
}

#thinupperline {
    width: 60%;
    margin: 30px auto;
    height: 0.5px;
    border-bottom: 1px solid rgba(255,255,255,.25);
}

#thinlowerline {
    width: 60%;
    margin: 0 auto;
    margin-top: 30px;
    height: 0.5px;
    border-top: 1px solid rgba(255,255,255,.25);
    padding-bottom: 100px;
}

.contact-holder {
    display: flex;
    flex-direction: row;
    width: 50%;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    margin-bottom: 0px;
    padding: 0;
}

.contact-holder p{
    color: #61dafb;
    font-size: 1.8em;
    margin-right: 15px;
    margin: 20px;
}

.contact-holder a{
    color: white;
    font-size: 1.5em;
    margin: 0;
    cursor: pointer;
    text-decoration: none;
}

.contact-holder a:hover {
    color: #61dafb;
}
.App {
  text-align: center;
  background-color: #282c34;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

